

.preloader {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--white);
  transition: opacity .5s, visibility .5s;
  &_hidden{
    opacity: 0;
    visibility: hidden;
  }

  &__icon {
    width: 100px;
    height: 100px;
  }
}
